nav{
    min-height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    button{
        background: transparent;
        border: none;
        cursor: pointer;
        border: 2px solid rgb(65, 65, 65);
        padding: 0.5rem ;
        transition: all 0.2 ease;
        &:hover{
            //TO DO: USE THIS LOGIC TO SET DARKMODE
            background:rgb(65, 65, 65) ;
            color: white;
        }
    }
}

@media screen and (max-width: 768px){
nav{
    button{
        z-index: 10;
    }
}
    
}